* {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif
  ;
}
body{
  padding: 60px;
}

input{
  font-size: 1.2rem;
  width: 100%;
  color: gray;
}

.card{
  align-content: center;
  border:1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin: 10px 0;
}

.card p{
  padding: 5px;
  text-align: center;
}
.card img{
  max-height: 170px;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}